.section-watch {
	margin-top: rem(97);
}

.cat_items {
	display: flex;
	flex-wrap: wrap;

	&__item {
		flex: 0 0 rem(240);
		max-width: rem(240);

		display: flex;
		flex-direction: column;

		margin-right: rem(28);

		border-radius: rem(5);
		overflow: hidden;

		&:last-child {
			margin-right: 0;
		}

		img {
			height: rem(200);
			object-fit: cover;
		}

		.text {
			padding: rem(16) rem(28);
			border-bottom: rem(0.5) solid var(--c-black-10);
			border-right: rem(0.5) solid var(--c-black-10);
			border-left: rem(0.5) solid var(--c-black-10);

			span {
				font-size: rem(16);
				font-weight: 500;
				color: #424242;
			}
		}
	}
}