.wrap_reviews {

}

.reviews_items {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(20);

	&__text_block {
		border-radius: rem(5);
		width: 100%;
		background-color: rgba(237, 237, 237, .2);
		padding: rem(28) rem(24);

		display: flex;
		flex-direction: column;

		margin-bottom: rem(24);

		p {
			font-size: rem(14);
			font-weight: 500;
			margin-bottom: rem(8);
		}

		a {
			font-size: rem(14);
			font-weight: 600;
			color: #4B7EFF;

			&:hover {
				text-decoration: underline;
			}
		}
	}

	&__item {
		border-radius: rem(5);
		border: rem(0.5) solid var(--c-black-20);
		margin-bottom: rem(24);

		display: flex;
		flex-wrap: wrap;

		.left, .right {
			padding: rem(24);
		}

		.left {
			flex: 0 0 rem(516);
			max-width: rem(516);

			border-right: rem(0.5) solid var(--c-black-20);

			display: flex;
			flex-direction: column;

			&__star {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				margin-bottom: rem(12);

				.flex {
					display: flex;
					flex-wrap: wrap;
					align-items: center;

					i {
						font-size: rem(12);
						margin-right: rem(2);

						&:last-child {
							margin-right: 0;
						}
					}

					.inactive {
						&::before {
							color: #595959;
						}
					}
				}

				span {
					margin-left: rem(7);
					font-size: rem(10);
					color: var(--c-black-40);
					// line-height: 130%;
				}
			}

			&__text {
				display: flex;
				flex-direction: column;

				b {
					line-height: 130%;
					font-size: rem(14);
					margin-bottom: rem(12);
				}

				i {
					margin-bottom: rem(4);
					font-size: rem(18);
				}

				p {
					color: #212121;
					line-height: 162%;
				}
			}

			&__rating {
				margin-top: auto;

				display: flex;
				flex-wrap: wrap;
				align-items: center;

				> span {
					font-size: rem(12);
					color: var(--c-black-50);
					line-height: 130%;
					margin-right: rem(34);
				}

				.flex {
					align-items: center;

					.rating-button {
						margin-right: rem(20);

						&:last-of-type {
							margin-right: 0;
						}
					}
				}
			}
		}

		.right {
			flex: 0 0 rem(376);
			max-width: rem(376);

			&__slider-main {
				height: rem(200);
				border-radius: rem(2);
				overflow: hidden;
				margin-bottom: rem(15);

				.swiper-slide {

					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}

					.not-image {
						height: 100%;

						span {
							font-size: rem(16);
						}
					}
				}

				
			}

			&__slider-thumbs {
				.swiper-slide {
					height: rem(65);
					border-radius: rem(2);
					overflow: hidden;

					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}

					.not-image {
						height: 100%;
					}
				}
			}
		}
	}
}

.rating-button {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	i {
		font-size: rem(22);
		color: var(--c-black-50);
		margin-right: rem(3);
	}

	span {
		font-size: rem(14);
		line-height: 130%;
	}
}
.rating-button.rating-plus {
	i {
		margin-bottom: rem(5);
	}
	span {
		color: #15BC58;
	}
}
.rating-button.rating-minus {
	i {
		margin-top: rem(5);
		transform: rotate(-180deg);
	}
	span {
		color: #FF0000;
	}
}