.breadcrumbs {
	margin-bottom: rem(34);

	&__items {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		a, span {
			font-size: rem(12);
			// line-height: 160%;
		}

		a {
			position: relative;
			color: var(--c-black-50);
			margin-right: rem(18);

			&:hover {
				color: var(--c-red);
			}

			&::after {
				content: "\e914";
				position: absolute;
				pointer-events: none;
				top: rem(5);
				right: rem(-10);
				// margin: 0 rem(8);
				font-family: 'icons';
				font-size: rem(6);
				color: var(--c-black-50);
			}
		}
	}
}