.cat_items {
	display: flex;
	flex-wrap: wrap;

	margin-bottom: rem(88);

	&__block {
		width: calc((100% - 3rem) / 3);
		margin-right: rem(24);
		margin-bottom: rem(24);

		border-radius: rem(5);
		overflow: hidden;
		border: rem(0.5) solid rgba(0, 0, 0, 0.1);
		
		transition: all 250ms ease;

		&:hover {
			box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
			0px 4px 4px rgba(0, 0, 0, 0.25);

			.text p {
				color: var(--c-red);
			}
		}

		&:nth-child(3n) {
			margin-right: 0;
		}

		img {
			width: 100%;
			height: rem(203);
			object-fit: cover;
		}

		.text {
			padding: rem(16) rem(24);
			background-color: white;

			p {
				font-size: rem(16);
				font-weight: 500;
				color: #424242;
			}
		}
	}
}

.catalog {

	.group-items-3 {
		.item_product {
			margin-top: rem(54);

			&:first-child, &:nth-child(2), &:nth-child(3) {
				margin-top: 0;
			}
		}
	}
	

	.section-header {
		.title-24 {
			margin-bottom: 0;
		}
	}

	.tab_wrap {
		margin-bottom: rem(29);
		&__nav {
			margin-bottom: 0;
			ul {
				justify-content: space-between;

				.tab_btn {

					// margin-right: rem(47);

					a {
						font-size: rem(12);
					}
				}
			}
		}
	}

	.filter_wrap {
		margin-bottom: rem(29);
	}
}


.filter_wrap {

	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;

	> ul {
		display: flex;
		flex-wrap: wrap;

		> li {
			position: relative;
			margin-right: rem(17);

			

			&:last-child {
				margin-right: 0;
			}

			> a {
				// position: relative;
				display: flex;
				align-items: center;
				color: var(--c-black-50);

				font-weight: 500;
				font-size: rem(12);

				height: rem(25);
				padding: 0 rem(16);
				border: rem(1) solid var(--c-black-50);

				border-radius: rem(100);

				&:hover {
					background-color: var(--c-red);
					color: white;
					border-color: var(--c-red);

					i {
						&::before {
							color: white;
						}
					}
				}

				i {
					pointer-events: none;
					margin-left: rem(7);
					font-size: rem(6);

					&::before {
						color: var(--c-black-50);
					}
				}
			}

			.filter_dropmenu {
				position: absolute;
				top: 100%;
				left: 0;

				margin-top: rem(7);
				width: rem(297);
				border-radius: rem(20);
				padding: rem(20);

				background-color: white;
				box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
				0px 4px 4px rgba(0, 0, 0, 0.25);
				z-index: 10;

				// display: flex;
				flex-wrap: wrap;

				display: none;

				li {
					margin-top: rem(19);

					&:nth-child(2n-1) {
						width: 100%;
						max-width: rem(160);
					}
					
					&:first-child, &:nth-child(2) {
						margin-top: 0;
					}

					a {
						color: var(--c-black-50);

						&:hover {
							color: black;
						}
					}
				}
			}
		}

		> li.active {

			> a {
				border-color: white;
				box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
				0px 4px 4px rgba(0, 0, 0, 0.25);
				color: black;

				&:hover {
					background-color: white;
					border-color: white;
					color: black;
				}

				i {
					transform: rotate(180deg);

					&::before {
						color: var(--c-red);
					}
				}
			}

			.filter_dropmenu {
				display: flex;
			}
		}
	}

	&__sort {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		.title {
			color: var(--c-black-60);
			font-size: rem(12);
			margin-right: rem(7);
		}

		.select_target {
			display: flex;
			align-items: center;

			span {
				font-size: rem(12);
				color: black;
				margin-right: rem(7);
			}

			i {
				font-size: rem(6);

				&::before {
					color: black;
				}
			}
		}

		.filter_sort_drop {
			position: absolute;
			top: 100%;
			margin-top: rem(7);
			right: 0;
			background-color: white;
			padding: rem(20);
			border-radius: rem(20);
			box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
			0px 4px 4px rgba(0, 0, 0, 0.25);
			z-index: 10;
			display: none;

			ul {
				display: flex;
				flex-direction: column;

				li {
					margin-bottom: rem(10);
					&:last-child {
						margin-bottom: 0;
					}
					a {
						color: var(--c-black-50);

						&:hover {
							color: black;
						}
					}
				}
			}
		}

		
	}

	&__sort.active {
		.select_target {
			span {
				color: var(--c-red);
			}

			i {
				transform: rotate(180deg);

				&::before {
					color: var(--c-red);
				}
			}
		}

		.filter_sort_drop {
			display: block;
		}
	}
}