.wrap_works {

	.section-header {
		margin-top: rem(36);
		margin-bottom: rem(54);

		p {
			color: var(--c-black-70);
			font-size: rem(14);
		}
	}

	p.w-text {
		max-width: rem(889);
		font-size: rem(12);
	}

	.page_nav {
		margin-top: rem(36);
	}
}

.work_items {
	display: flex;
	flex-wrap: wrap;

	&__item {
		width: calc((100% - 24px) / 2);
		margin-right: rem(24);

		margin-bottom: rem(52);

		&:nth-child(2n) {
			margin-right: 0;
		}

		&:hover {
			.image {
				box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
				0px 4px 4px rgba(0, 0, 0, 0.25);

				.button-red {
					opacity: 1;
				}

				&::before {
					opacity: 1;
				}
			}
		}

		.image {
			position: relative;
			border-radius: rem(5);
			height: rem(256);
			overflow: hidden;

			transition: all 250ms ease;

			display: flex;
			align-items: center;
			justify-content: center;

			margin-bottom: rem(20);

			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;

				transition: all 250ms ease;

				background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));

				opacity: 0;
			}

			.button-red {
				position: relative;
				z-index: 2;
				transition: all 250ms ease;
				opacity: 0;
			}

			img {
				position: absolute;
				top: 0;
				left: 0;

				width: 100%;
				height: 100%;
				object-fit: cover;

				z-index: -1;
			}
		}

		.text {
			.title {
				font-weight: 600;
				font-size: rem(16);
				color: black;
				margin-bottom: rem(13);
			}

			p {
				color: var(--c-black-70);
				line-height: 160%;
				font-size: rem(14);
			}
		}
	}
}