.wrap_articles {
	
}

.article_items {
	display: flex;
	// flex-wrap: wrap;
	flex-direction: column;

	margin-bottom: rem(30);

	&__item {
		display: flex;
		flex-wrap: wrap;

		border-radius: rem(5);
		height: rem(235);
		overflow: hidden;
		border: rem(0.5) solid var(--c-black-20);

		margin-bottom: rem(24);

		transition: all 250ms ease;

		&:hover {
			box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
			0px 4px 4px rgba(0, 0, 0, 0.25);
		}

		.left, .right {
			flex: 1;
		}

		.left {
			padding: rem(24);
			display: flex;
			flex-direction: column;

			flex: 0 0 rem(447);
			max-width: rem(447);

			&__title {

				margin-bottom: rem(18);

				.title {
					font-size: rem(20);
					font-weight: 600;
					margin-bottom: rem(5);
				}
				span {
					font-size: rem(8);
					color: var(--c-black-50);
				}
			}

			&__text {
				p {
					font-size: rem(14);
					line-height: 140%;
					color: var(--c-black-70);
				}
			}

			&__foot {
				margin-top: auto;

				display: flex;
				flex-wrap: wrap;
				align-items: center;
				justify-content: space-between;

				a {
					font-size: rem(10);
					line-height: 148%;
					color: #535353;
					text-decoration: underline;

					&:hover {
						color: var(--c-red);
					}
				}

				span {
					display: flex;
					flex-grow: wrap;
					align-items: center;

					font-size: rem(8);
					font-weight: 500;

					color: var(--c-black-50);

					i {
						font-size: rem(9);

						margin-right: rem(4);

						&::before {
							color: var(--c-black-50);
						}
					}


				}
			}
		}

		.right {
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				vertical-align: middle;
			}
		}
	}
}