.page_nav {
	display: flex;
	flex-direction: column;

	margin-bottom: rem(124);

	&__top {
		margin-bottom: rem(44);

		a {
			width: 100%;
			height: rem(50);
			background-color: rgba(196, 196, 196, 0.2);

			display: flex;
			justify-content: center;
			align-items: center;

			font-weight: 500;
			font-size: rem(12);
		}
	}

	&__button {
		height: rem(34);
		display: flex;
		align-items: center;

		padding: 0 rem(18);
		background-color: white;
		border-radius: rem(1);
		transform: all 250ms ease;

		&:hover {
			background-color: var(--c-red);
			
			span {
				color: white;
			}

			i {
				&::before {
					color: white;
				}
			}
		}

		

		span {
			color: #777777;
			line-height: 130%;
			font-weight: 600;
		}

		i {
			// margin-top: rem(3);
			font-size: rem(10);

			&::before {
				color: #777777;
			}
		}
	}

	&__bottom {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;
		margin-bottom: rem(16);
		

		.prev {
			i {
				margin-right: rem(6);
			}
		}

		.next {
			i {
				margin-left: rem(6);
			}
		}

		ul {
			margin: 0 rem(40);

			display: flex;
			flex-wrap: wrap;
			align-items: center;

			li {

				a {
					width: rem(34);
					height: rem(34);
					border-radius: rem(5);
					font-size: rem(12);
					color: #777777;
					display: flex;
					align-items: center;
					justify-content: center;

					border: rem(1) solid transparent;

					&:hover {
						color: black;
					}
				}

				.dots {
					font-size: rem(12);
					color: #777777;
					margin: 0 rem(14);
				}
			}

			li.active {
				a {
					color: black;
					border-color: var(--c-red);
				}
			}
		}
	}

	&__all {
		display: flex;
		justify-content: center;

		a {
			font-size: rem(12);
			line-height: 130%;
			color: #3A3A3A;

			display: flex;
			align-items: center;

			&:hover {
				color: var(--c-red);

				i {
					&::before {
						color: var(--c-red);
					}
				}
			}

			i {
				margin-left: rem(3);
				font-size: rem(6);

				&::before {
					color: #3A3A3A;
				}
			}
		}
	}
}