.product {
	&__slider {
		flex: 0 0 rem(588);
		max-width: rem(588);

		.not-image {
			height: rem(65);
		}

		&-main {
			height: rem(366);
			margin-bottom: rem(20);

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

			.not-image {
				height: rem(366);
				span {
					font-size: rem(16);
				}
			}
		}

		&-thumbs {
			height: rem(65);

			.swiper-slide {
				cursor: pointer;
			}

			img {
				width: rem(100);
				height: rem(65);
				object-fit: cover;
				border-radius: rem(2);
			}
		}
	}

	&__list {
		padding-bottom: rem(26);
		margin-bottom: rem(22);
		border-bottom: rem(0.5) solid var(--c-black-20);

		ul {
			display: flex;
			flex-direction: column;

			li {
				display: flex;
				flex-wrap: wrap;
				margin-bottom: rem(10);

				&:last-child {
					margin-bottom: 0;
				}

				b,
				span {
					font-size: rem(10);
					line-height: 120%;
				}

				b {
					flex: 0 0 rem(140);
					max-width: rem(140);
					font-weight: 600;
				}
			}
		}
	}

	&__info {
		margin-left: rem(24);
		flex: 0 0 rem(280);
		max-width: rem(280);

		display: flex;
		flex-direction: column;

		.title-24 {
			padding-bottom: rem(20);
			margin-bottom: rem(26);
			border-bottom: rem(0.5) solid var(--c-black-20);
		}

		&-price {
			padding-bottom: rem(30);
			margin-bottom: rem(26);
			border-bottom: rem(0.5) solid var(--c-black-20);

			display: flex;
			flex-direction: column;

			.title-18 {
				margin-bottom: rem(6);
			}

			.subtitle {
				font-size: rem(12);
				color: var(--c-black-20);
				margin-bottom: rem(12);
			}

			.button-icon {
				align-self: baseline;
			}

		}

		

		&-buttons {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.button-red {
				margin-right: rem(18);
			}
		}
	}


	.tab_wrap {
		margin-top: rem(77);
		margin-bottom: rem(34);

		&__nav {
			margin-bottom: rem(32);
		}
	}

	.section-related {

		// .section-header {
		// 	margin-bottom: rem(26);
		// }
	}
}



.not-image {
	border-radius: rem(2);
	background-color: white;
	border: rem(0.5) solid var(--c-black-20);
	
	display: flex;
	align-items: center;
	justify-content: center;

	span {
		font-size: rem(10);
		color: var(--c-black-20);
		text-transform: lowercase;
	}
}