*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'Inter';
  src: local("Inter Regular"), local("Inter-Regular"), url("../fonts/inter/Inter-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Inter';
  src: local("Inter Medium"), local("Inter-Medium"), url("../fonts/inter/Inter-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Inter';
  src: local("Inter Semi Bold"), local("Inter-SemiBold"), url("../fonts/inter/Inter-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Inter';
  src: local("Inter Bold"), local("Inter-Bold"), url("../fonts/inter/Inter-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Inter';
  src: local("Inter Black"), local("Inter-Black"), url("../fonts/inter/Inter-Black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

:root {
  --default-font: 'Inter';
  --c-text: #212121;
  --c-text-60: rgba(33,33,33,.6);
  --c-blue: #0047FF;
  --c-red: #FF0000;
  --c-gray: #8D8D8D;
  --c-gray-field: #757575;
  --c-black-80: rgba(0, 0, 0, .8);
  --c-black-70: rgba(0,0,0,.7);
  --c-black-60: rgba(0,0,0,.6);
  --c-black-50: rgba(0, 0, 0, .5);
  --c-black-40: rgba(0, 0, 0, .4);
  --c-black-30: rgba(0, 0, 0, .3);
  --c-black-20: rgba(0, 0, 0, .2);
  --c-black-10: rgba(0, 0, 0, .1);
}

* {
  outline: none;
  border: 0;
}

html, body {
  position: relative;
  overflow-x: hidden;
}

html {
  font-size: 100%;
}

a, a span, input, button, button span, select, i, i::before {
  transition: all 250ms ease;
}

button {
  background-color: transparent;
}

a {
  text-decoration: none;
}

i {
  vertical-align: middle;
}

input, button {
  font-family: var(--default-font);
  font-size: 0.875rem;
}

b {
  font-weight: 500;
}

li {
  list-style: none;
}

body {
  font-size: 0.875rem;
  font-family: var(--default-font);
  min-width: 320px;
  color: #000000;
  -webkit-overflow-scrolling: touch;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.relative {
  position: relative;
}

.w-8\/12 {
  flex: 0 0 66.666667%;
  max-width: 66.666667%;
}

.w-7\/12 {
  flex: 0 0 58.333333%;
  max-width: 58.333333%;
}

.w-6\/12 {
  flex: 0 0 50%;
  max-width: 50%;
}

.w-5\/12 {
  flex: 0 0 41.666667%;
  max-width: 41.666667%;
}

.w-4\/12 {
  flex: 0 0 33.333333%;
  max-width: 33.333333%;
  margin-left: -1.5rem;
  margin-right: -1.5rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.w-full {
  width: 100%;
}

.h-full {
  height: 100%;
}

.block {
  display: block;
}

.flex {
  display: flex;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-col {
  flex-direction: column;
}

.flex-start {
  justify-content: flex-start;
}

.flex-end {
  justify-content: flex-end;
}

.flex-center {
  justify-content: center;
}

.flex-between {
  justify-content: space-between;
}

.items-center {
  align-items: center !important;
}

.items-start {
  align-items: flex-start !important;
}

.items-end {
  align-items: flex-end !important;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.wrap-col-2 {
  display: flex;
  flex-wrap: wrap;
}

.header {
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25), 0 4px 25px 0 rgba(0, 0, 0, 0.25);
  margin-bottom: 2rem;
}

.header__top {
  background-color: #f3f3f3;
  height: 2.5rem;
  padding: 0.625rem 0 0.8125rem 0;
}

.header__top .container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.header__top-change i {
  font-size: 1rem;
  margin-right: 0.15625rem;
}

.header__top-change .text {
  font-weight: 500;
}

.header__top-text {
  margin-right: auto;
  margin-left: 3.6875rem;
}

.header__top-text p {
  font-weight: 500;
  font-size: 0.75rem;
}

.header__top-nav ul {
  display: flex;
  flex-wrap: wrap;
}

.header__top-nav ul li {
  margin-right: 2.375rem;
}

.header__top-nav ul li:last-child {
