}

.header__center-search .form_search-button:active {
  opacity: 1;
}

.header__center-search .form_search-button i {
  font-size: 0.9375rem;
}

.header__center-contact {
  margin-right: 4.0625rem;
  padding-bottom: 0.3125rem;
}

.header__center-contact ul {
  display: flex;
  flex-direction: column;
}

.header__center-contact ul li {
  margin-bottom: 0.4375rem;
  color: var(--c-black-60);
  font-size: 0.75rem;
}

.header__center-contact ul li:last-child {
  margin-bottom: 0;
}

.header__center-contact ul li a {
  display: flex;
  align-items: center;
}

.header__center-contact ul li a:hover {
  color: black;
}

.header__center-contact ul li a:hover i::before {
  color: black;
}

.header__center-contact ul li a i {
  margin-right: 0.4375rem;
  font-size: 0.625rem;
  margin-top: 0.125rem;
}

.header__center-contact ul li a i::before {
  color: var(--c-black-60);
}

.header__center-phone {
  margin-right: 1.25rem;
}

.header__center-phone ul {
  display: flex;
  flex-direction: column;
}

.header__center-phone ul li {
  margin-bottom: 0.5rem;
}

.header__center-phone ul li:last-child {
  margin-bottom: 0;
}

.header__center-phone ul li a {
  font-size: 1rem;
  font-weight: 600;
}

.header__center-phone ul li a:hover {
  color: var(--c-red);
}

.header__center-button .button-red-border {
  padding: 0.90625rem 1.1875rem;
}

.header__nav {
  border-top: 0.03125rem solid #D2D2D2;
}

.header__nav ul {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-top: 0.3125rem;
  padding-bottom: 0.3125rem;
}

.header__nav ul > li {
  margin-right: 2rem;
}

.header__nav ul > li:last-child {
  margin-right: 0;
}

.header__nav ul > li:last-child a {
  padding-right: 0;
}

.header__nav ul > li:first-child {
  margin-right: auto;
}

.header__nav ul > li:first-child a {
  padding-left: 0;
}

.header__nav ul > li > a {
  font-size: 1rem;
  font-weight: 500;
  padding: 0.9375rem 1rem;
  border-radius: 0.3125rem;
  display: flex;
  align-items: center;
}

.header__nav ul > li > a:hover {
  color: var(--c-red);
}

.header__nav ul > li > a .burger {
  margin-right: 0.5rem;
}

.header__nav ul > li > a i {
  font-size: 0.375rem;
  margin-left: 0.25rem;
}

.header__nav ul .drop-menu {
  position: relative;
}

.header__nav ul .drop-menu-content {
  position: absolute;
  left: 0;
  top: 100%;
  z-index: 10;
  margin-top: 0.5625rem;
  width: 100%;
  background-color: white;
  box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 0.3125rem;
  display: none;
  flex-direction: column;
  overflow: hidden;
  padding: 0;
}

.header__nav ul .drop-menu-content li {
  width: 100%;
  transition: all 250ms ease;
  margin: 0;
}

.header__nav ul .drop-menu-content li:hover {
  background-color: rgba(196, 196, 196, 0.2);
}

.header__nav ul .drop-menu-content li:hover a {
  color: black;
}

.header__nav ul .drop-menu-content li a {
  padding: 0.9375rem 1.25rem;
  color: var(--c-black-50);
  font-size: 0.875rem;
  white-space: nowrap;
  box-shadow: none;
}

.header__nav ul .drop-menu.active > a {
  box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
  color: var(--c-red);
}

.header__nav ul .drop-menu.active > a i {
  transform: rotate(180deg);
}

.header__nav ul .drop-menu.active .drop-menu-content {
  display: flex;
}

.header__nav .burger {
  width: 0.75rem;
  height: 0.625rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header__nav .burger .line {
  width: 100%;
  height: 0.125rem;
  border-radius: 0.125rem;
  background-color: var(--c-red);
}

.sect_slider {
  margin-bottom: 0.875rem;
}

.sect_slider .swiper-container {
  border-radius: 0.625rem;
}

.sect_slider .swiper-container .swiper-pagination {
  position: absolute;
  width: auto;
  bottom: 2.5rem;
  left: 7.8125rem;
}

.sect_slider .swiper-container .swiper-pagination-bullet {
  opacity: 1;
  width: 4.25rem !important;
  border-radius: 0.125rem !important;
  height: 0.125rem !important;
  background-color: rgba(255, 255, 255, 0.5) !important;
  margin: 0 0.625rem 0 0 !important;
}

.sect_slider .swiper-container .swiper-pagination-bullet:last-child {
  margin: 0;
}

.sect_slider .swiper-container .swiper-pagination-bullet-active {
  background-color: white !important;
}

.sect_slider .swiper-container .swiper-nav-prev,
.sect_slider .swiper-container .swiper-nav-next {
  position: absolute;
  top: 50%;
  z-index: 10;
  margin-top: -0.5625rem;
}

.sect_slider .swiper-container .swiper-nav-prev {
  left: 1.125rem;
}

.sect_slider .swiper-container .swiper-nav-next {
  right: 1.125rem;
}

.sect_slider__item {
  position: relative;
  height: 24.75rem;
  padding: 5.125rem 7.8125rem;
  border-radius: 0.625rem;
  background: linear-gradient(89.57deg, rgba(0, 0, 0, 0.63) 11.27%, rgba(125, 125, 125, 0.24) 99.68%);
}

.sect_slider__item-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: -1;
}

.sect_slider__item .title-32 {
  color: white;
}

.sect_slider__item .text, .sect_slider__item .button-gray {
  margin-top: 0.875rem;
}

.sect_slider__item .text {
  max-width: 23.75rem;
  font-weight: 500;
  line-height: 140%;
  color: white;
}

.swiper-nav-wrap {
  display: flex;
  flex-wrap: wrap;
}

.swiper-nav-wrap .swiper-nav-prev {
  margin-right: 0.6875rem;
}

.swiper-nav-prev,
.swiper-nav-next {
  width: 2.75rem;
  height: 2.75rem;
  border-radius: 50%;
  border: solid 0.03125rem rgba(0, 0, 0, 0.1);
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25), 0 4px 25px 0 rgba(0, 0, 0, 0.25);
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.swiper-nav-prev i,
.swiper-nav-next i {
  font-size: 0.6875rem;
}

.swiper-nav-next i {
  margin-left: 0.1875rem;
}

.swiper-button-disabled i::before {
  color: #595959;
  opacity: .3;
}

.swiper-pagination {
  display: flex;
  flex-wrap: wrap;
}

.swiper-pagination-bullet {
  opacity: 1 !important;
  width: 2.3125rem !important;
  height: 0.125rem !important;
  border-radius: 0.125rem !important;
  background-color: var(--c-black-50) !important;
  margin-right: 0.75rem;
}

.swiper-pagination-bullet:last-child {
  margin-right: 0;
}

.swiper-pagination-bullet-active {
  background-color: black !important;
}

.advantages {
  margin-bottom: 3.75rem;
}

.advantages__items {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.advantages__item {
  width: calc((100% - 4.125rem) / 4);
  margin-right: 1.375rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.advantages__item:last-child {
  margin-right: 0;
}

.advantages__item img {
  margin-right: 0.625rem;
  width: 1.875rem;
}

.advantages__item i {
  margin-right: 0.625rem;
  font-size: 1.875rem;
}

.advantages__item p {
  font-size: 0.75rem;
}

.cat_items {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 5.5rem;
}

.cat_items__block {
  width: calc((100% - 3rem) / 3);
  margin-right: 1.5rem;
  margin-bottom: 1.5rem;
  border-radius: 0.3125rem;
  overflow: hidden;
  border: 0.03125rem solid rgba(0, 0, 0, 0.1);
