}

.cat_items__block img {
  width: 100%;
  height: 12.6875rem;
  object-fit: cover;
}

.cat_items__block .text {
  padding: 1rem 1.5rem;
  background-color: white;
}

.cat_items__block .text p {
  font-size: 1rem;
  font-weight: 500;
  color: #424242;
}

.catalog .group-items-3 .item_product {
  margin-top: 3.375rem;
}

.catalog .group-items-3 .item_product:first-child, .catalog .group-items-3 .item_product:nth-child(2), .catalog .group-items-3 .item_product:nth-child(3) {
  margin-top: 0;
}

.catalog .section-header .title-24 {
  margin-bottom: 0;
}

.catalog .tab_wrap {
  margin-bottom: 1.8125rem;
}

.catalog .tab_wrap__nav {
  margin-bottom: 0;
}

.catalog .tab_wrap__nav ul {
  justify-content: space-between;
}

.catalog .tab_wrap__nav ul .tab_btn a {
