.filter_wrap__sort .filter_sort_drop {
  position: absolute;
  top: 100%;
  margin-top: 0.4375rem;
  right: 0;
  background-color: white;
  padding: 1.25rem;
  border-radius: 1.25rem;
  box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
  z-index: 10;
  display: none;
}

.filter_wrap__sort .filter_sort_drop ul {
  display: flex;
  flex-direction: column;
}

.filter_wrap__sort .filter_sort_drop ul li {
  margin-bottom: 0.625rem;
}

.filter_wrap__sort .filter_sort_drop ul li:last-child {
  margin-bottom: 0;
}

.filter_wrap__sort .filter_sort_drop ul li a {
  color: var(--c-black-50);
}

.filter_wrap__sort .filter_sort_drop ul li a:hover {
  color: black;
