.filter_wrap__sort.active .select_target span {
  color: var(--c-red);
}

.filter_wrap__sort.active .select_target i {
  transform: rotate(180deg);
}

.filter_wrap__sort.active .select_target i::before {
  color: var(--c-red);
}

.filter_wrap__sort.active .filter_sort_drop {
