.wrap_article {
	position: relative;

	.back_link {
		position: absolute;
		top: rem(-50);
		left: 0;
	}

	h1 {
		font-size: rem(24);
		font-weight: 600;
		margin-bottom: rem(18);
	}

	h2 {
		font-size: rem(18);
		font-weight: 600;
		
		margin-bottom: rem(18);
	}

	p {
		font-size: rem(14);
		line-height: 160%;
		color: var(--c-black-70);
		margin-bottom: rem(24);
	}

	.image {
		width: 100%;
		margin-bottom: rem(70);

		img {
			width: 100%;
		}

		span {
			margin-top: rem(14);
			font-size: rem(10);
			color: var(--c-black-70);
		}
	}

	

	

	blockquote {
		margin-bottom: rem(40);
		
		.title-36 {
			font-size: rem(36);
			line-height: 160%;
			// margin-bottom: rem(18);
			font-weight: normal;
		}
		span {
			font-size: rem(10);
			line-height: 160%;
			color: var(--c-black-70);
		}
	}

}

.back_link {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	display: inline-block;

	&:hover {
		svg {
			path {
				stroke: var(--c-red);
			}
		}

		span {
			color: var(--c-red);
		}
	}

	svg {
		path {
			stroke: var(--c-black-50);
		}
	}

	span {
		color: var(--c-black-50);
		margin-left: rem(6);
		font-size: rem(14);
	}
}