.wrap_calc {



	.section-header {
		margin-top: rem(36);
		p {
			color: var(--c-black-70);
			font-size: rem(14);
		}
	}

	p.w-text {
		max-width: rem(889);
		font-size: rem(12);
	}
}

.calc_item {
	position: relative;
	flex: 0 0 rem(588);
	max-width: rem(588);
	height: rem(256);
	border-radius: rem(5);
	overflow: hidden;


	display: flex;
	justify-content: center;
	align-items: center;

	transition: all 250ms ease;

	&:hover {
		box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
		0px 4px 4px rgba(0, 0, 0, 0.25);
	}
	
	&__image {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: var(--c-black-60);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__text {
		display: flex;
		flex-direction: column;
		align-items: center;
		
		.subtitle {
			font-size: rem(10);
			color: rgba(255,255,255,.5);
			margin-bottom: rem(13);
		}

		.title-24 {
			margin-bottom: rem(13);
			color: white;
		}
	}
}

.form_calc {
	display: flex;
	flex-wrap: wrap;

	height: rem(573);

	border-radius: rem(10);
	margin-top: rem(64);
	overflow: hidden;

	background-color: white;
	box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
	0px 4px 4px rgba(0, 0, 0, 0.25);

	&__left {
		position: relative;
		flex: 0 0 rem(244);
		max-width: rem(244);
		height: rem(573);

		background-color: #383E42;

		padding-top: rem(62);
		padding-left: rem(25);

		.static {
			display: block;
		}

		.end {
			display: none;

			padding-left: rem(17);

			img {
				position: relative;
				bottom: auto;
				left: auto;

				width: rem(159);
				height: rem(159);
				margin-bottom: rem(40);
			}

			.title-24 {
				font-size: rem(24);
				font-weight: bold;
				line-height: 134%;
				max-width: rem(183);
				margin-top: rem(25);
				margin-bottom: rem(46);
				display: block;
			}

			&__list {
				display: flex;
				flex-direction: column;
				span {
					font-weight: bold;
					line-height: 110%;
					color: white;
					// display: block;
					margin-bottom: rem(17);
				}

				ul {
					li {
						position: relative;
						margin-bottom: rem(7);
						padding-left: rem(12);

						&::before {
							content: "";
							position: absolute;
							top: 0;
							left: 0;
							width: rem(1);
							height: rem(19);
							background-color: rgba(255,255,255,.4);
						}

						&:last-child {
							margin-bottom: 0;
						}

						font-weight: 500;
						line-height: 160%;
						color: white;
					}
				}
			}
		}

		img {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			object-fit: cover;
		}

		b {
			font-weight: 900;
			color: white;
			font-size: rem(42);
			margin-bottom: rem(8);
			
		}

		p {
			font-weight: 500;
			color: white;
			max-width: rem(175);
		}
	}

	&__right {
		flex: 1;

		display: flex;
		flex-direction: column;

		&-header {
			display: flex;
			flex-direction: column;
			align-items: center;

			margin-top: rem(35);
			// margin-bottom: rem(82);

			padding: 0 rem(57);

			p {
				color: rgba(54, 54, 54, .6);
				line-height: 150%;
				margin-bottom: rem(16);
			}

			.bar {
				display: flex;
				flex-wrap: wrap;
				width: 100%;
				margin-bottom: rem(30);

				li {
					margin-right: rem(24);

					&:last-child {
						margin-right: 0;
					}

					.line {
						// background-color: #CBDBDF;
						border-radius: rem(5);
						overflow: hidden;
						width: 100%;
						display: block;
						height: rem(6);

						background: #CBDBDF url(../img/calc/line.png) repeat-x;
					}
				}

				.active {
					.line {
						background: var(--c-red) url(../img/calc/line.png) repeat-x;
					}
				}
			}

			.bar-col-4 {
				li {
					width: calc((100% - 4.5rem) / 4);
				}
			}

			.title-18 {
				margin-bottom: rem(5);
				font-weight: bold;
			}

			.bar_count {
				

				span {
					font-weight: 500;
					line-height: 150%;
					color: rgba(54, 54, 54, .6);
				}
			}
		}

		&-tab {
			display: none;
			
		}

		&-tab.active {
			display: flex;
			flex-direction: column;
			height: 100%;
		}

		&-tab-message {

			.form_calc__right-text-center {
				margin-left: auto;
				margin-right: auto;
				margin-top: rem(87);

				max-width: rem(340);

				.title {
					font-size: rem(32);
					line-height: 134%;
					font-weight: bold;
					display: block;
					margin-bottom: rem(24);
					color: #363636;
				}

				.subtitle {
					font-size: rem(28);
					font-weight: 600;
					display: block;
					margin-bottom: rem(4);
					color: #363636;
				}

				p {
					color: var(--c-black-50);
					font-size: rem(14);
				}

				.field-set {
					margin-top: rem(40);
					margin-bottom: rem(12);
				}

				.button-red {
					width: 100%;
					height: rem(50);
					display: flex;
					justify-content: center;

					span {
						font-size: rem(12);
						font-weight: 600;
					}
				}

				.field-checked {
					margin-top: rem(20);

					.check {
						width: rem(13);
						height: rem(13);

						&::before {
							font-size: rem(12) !important;
							position: absolute;
							top: -0.125rem;
							left: 0;
							font-size: 0.75rem !important;
						}
					}

					.text {
						font-size: rem(10);
					}
				}
			}

			.form_calc__right-button {
				justify-content: flex-start;
				padding-left: rem(57);
			}
		}

		&-select {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;

			padding: 0 rem(60);

			margin-top: rem(82);

			.item {
				display: flex;
				flex-direction: column;
				align-items: center;

				margin-right: rem(75);

				&:last-child {
					margin-right: 0;
				}

				img {
					margin-bottom: rem(42);

					width: rem(104);

				}

				&__text {
					display: flex;
					flex-direction: column;
					align-items: center;

					b {
						font-weight: 600;
						line-height: 150%;
					}

					span {
						font-size: rem(10);
						line-height: 150%;
						color: rgba(31, 31, 31, .5);
						margin-bottom: rem(16);
					}

					.field {
						height: rem(30);
						width: rem(75);
						text-align: center;
					}
				}
			}

			.item_nth-2 {
				position: relative;
				top: rem(-22);

				img {
					margin-bottom: rem(30);
				}
			}

			.item_box {
				cursor: pointer;
				position: relative;
				border-radius: rem(5);
				
				width: 100%;
				max-width: rem(272);
				height: rem(132);

				margin-right: rem(10);
				margin-bottom: rem(13);


				display: flex;
				align-items: center;
				justify-content: center;

				overflow: hidden;

				transition: all 250ms ease;

				&:hover {
					box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
					0px 4px 4px rgba(0, 0, 0, 0.25);
				}

				&:nth-child(3n) {
					margin-right: 0;
				}

				input {
					display: none;

					&:checked ~ .check {
						opacity: 1;
					}

					&:checked ~ img {
						opacity: 1;
					}

					&:checked~.not_image {
						background-color: rgb(98, 98, 98);
					}
				}

				img {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					object-fit: cover;

					opacity: .4;
				}

				.not_image {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					display: block;
					background-color: rgba(98, 98, 98, .4);
				}

				.check {
					position: absolute;
					top: rem(12);
					right: rem(15);

					width: rem(27);
					height: rem(27);
					border-radius: 50%;
					background-color: white;

					display: flex;
					align-items: center;
					justify-content: center;

					opacity: 0;

					transition: all 250ms ease;

					i {
						font-size: rem(7);
					}
				}

				.title-18 {
					position: relative;
					color: white;
					font-weight: bold;
					z-index: 5;
				}
			}
		}

		&-button {
			display: flex;
			justify-content: center;

			margin-top: auto;
			margin-bottom: rem(28);

			.disabled {
				pointer-events: none;
				cursor: default;
			}

			.button-prev {
				margin-right: rem(27);

				display: flex;
				align-items: center;

				&:hover {
					span, i::before {
						color: var(--c-red);
					}
				}

				span {
					font-weight: 600;
					color: var(--c-black-40);
				}

				i {
					margin-right: rem(20);
					font-size: rem(21);

					transform: rotate(-90deg);

					&::before {
						color: var(--c-black-40);
					}
				}
			}

			.button-red {
				height: rem(46);
				padding: rem(14) rem(24);
				border-radius: rem(5);
				
				span {
					font-weight: 600;
					font-size: rem(14);
					text-transform: lowercase;
				}
			}
		}
	}
}

.form_calc_two {


	.form_calc__right-text-center {
		margin-top: rem(113);
	}

	.bar-col-4 {
		li {
			width: calc((100% - 7.5rem) / 6);
		}
	}

	.tab-1, .tab-2 {

		.form_calc__right-select {
			margin-top: rem(52);
		}

		.item {
			margin-right: rem(16);

			&:last-child {
				margin-right: 0;

				border-left: rem(0.5) dashed #88A5B7;
			}

			img {
				width: rem(153);
				height: rem(172);
				margin-bottom: rem(16);
			}
		}
	}

	.tab-3 {
		img {
			margin-bottom: rem(34);
		}

		.field-radio {
			margin-right: rem(42);

			&:last-child {
				margin-right: 0;
			}
		}
	}

	.tab-4 {

		.mt-24 {
			margin-top: rem(38);
		}

		.item__image {
			width: rem(244);
			height: rem(214);
			margin-left: rem(24);
			margin-right: rem(42);
		}

		.item__text {
			margin-right: rem(40);

			&:nth-child(4) {
				margin-right: 0;
			}
		}

		.item {
			border-left: rem(0.5) dotted #88A5B7;
			align-items: flex-start;
			margin-left: rem(45);

			padding-left: rem(22);
		}
	}
}

.form_calc--finish {

	.form_calc_two {
		.form_calc__left {
			padding-top: 0;
			display: flex;
			flex-direction: column;
			justify-content: center;
		}
	}

	.form_calc__left {
		.static {
			display: none;
		}

		.end {
			display: block;
		}
	}

	.form_calc__right-header {
		display: none;
	}
}


.field-radio {
	display: flex;
	align-items: center;

	input {
		display: none;

		&:checked ~ .check {
			&::before {
				opacity: 1;
			}
		}

		&:checked ~ .text {
			text-shadow: 0 0 .65px #1F1F1F,
			0 0 .65px #1F1F1F;
		}
	}

	.check {
		width: rem(16);
		height: rem(16);
		border-radius: 50%;
		border: rem(1) solid #586C8B;

		display: flex;
		align-items: center;
		justify-content: center;

		margin-right: rem(7);

		&::before {
			content: "";
			width: rem(8);
			height: rem(8);
			border-radius: 50%;
			background-color: var(--c-red);

			transition: all 250ms ease;

			opacity: 0;
		}
	}

	.text {
		color: #1F1F1F;
	}
}